/**
 *  user mixin module
 *  @module $my/code/mixin/user
 *  @author coder
 */
import {
  fetchUser,
  getUser,
  addUser,
  updateUser,
  removeUser,
  batchUser,
  login,
  logout
} from '$my/code/api/user'

// mixin
export default {

  /**
   * user
   * @member user
   * @property {Array} [list=\[\]] user
   * @property {number} [page=null] user
   * @property {number} [limit=null] user
   * @property {number} [total=0] user
   * @property {object} [model=null] user
   */
  data() {
    return {
      user: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * user
     * @function fetchUser
     * @param {object} data data
     * @param {object} [options] ajax
     * @returns {Promise} Promise
     */
    fetchUser({
      data,
      options
    } = {}) {
      return fetchUser(data, options).then(res => {
        this.user.list = res.list || []
        this.user.page = Number.parseInt(res.page) || 0
        this.user.limit = Number.parseInt(res.limit) || 0
        this.user.total = Number.parseInt(res.total) || 0
        return res
      })
    },
    /**
     * user
     * @function getUser
     * @param {string|number} id URLid
     * @param {object} data data
     * @param {object} [options] ajax
     * @returns {Promise} Promise
     */
    getUser({
      id,
      data,
      options
    } = {}) {
      return getUser(id, data, options).then(res => {
        this.user.model = Object.assign({}, data, res || {})
        return res
      })
    },
    /**
     * user
     * @function addUser
     * @param {object} data data
     * @param {number} [index] 
     * @param {object} [options] ajax
     * @returns {Promise} Promise
     */
    addUser({
      data,
      index,
      options
    } = {}) {
      return addUser(data, index, options).then(res => {
        const model = Object.assign({}, data, res || {})
        const list = [...this.user.list]
        if (index !== undefined) {
          list.splice(index, 0, model)
        } else {
          list.push(model);
        }
        this.user.list = list
        this.user.total += 1;
        return res
      })
    },
    /**
     * user
     * @function updateUser
     * @param {object} data data
     * @param {number} [index] index
     * @param {object} [options] ajax
     * @returns {Promise} Promise
     */
    updateUser({
      data,
      index,
      options
    } = {}) {
      return updateUser(data, index, options).then(res => {
        const model = Object.assign({}, data, res || {})
        const list = [...this.user.list]
        if (index !== undefined) {
          list.splice(index, 1, model)
        }
        this.user.list = list
        return res
      })
    },
    /**
     * user
     * @function removeUser
     * @param {string|number} id URLid
     * @param {object} data data
     * @param {number} [index] index
     * @param {object} [options] ajax
     * @returns {Promise} Promise
     */
    removeUser({
      id,
      data,
      index,
      options
    } = {}) {
      return removeUser(id, data, options).then(res => {
        const list = [...this.user.list]
        if (index !== undefined) {
          list.splice(index, 1)
        }
        this.user.list = list
        this.user.total -= 1;
        return res
      })
    },
    /**
     * user
     * @function batchUser
     * @param {object} data data
     * @param {number[]} [indexes] indexes
     * @param {object} [options] ajax
     * @returns {Promise} Promise
     */
    batchUser({
      data,
      indexes = [],
      options
    } = {}) {
      return batchUser(data, options).then(res => {
        if (indexes.length > 0) {
          this.user.list = this.user.list.filter((item, index) => !indexes.includes(index))
        }
        this.user.total -= indexes.length;
        return res
      })
    },
    /**
     * user LOGIN
     * @function login
     * @param {object} data data
     * @param {object} [options] ajax
     * @returns {Promise} Promise
     */
    login({
      data,
      options
    } = {}) {
      return login(data, options).then(res => {
        return res
      })
    },
    /**
     * user LOGOUT
     * @function logout
     * @param {object} data data
     * @param {object} [options] ajax
     * @returns {Promise} Promise
     */
    logout({
      data,
      options
    } = {}) {
      return logout(data, options).then(res => {
        return res
      })
    }
  }
}
